<template>
  <div class="create-profile">
    <div class="contianer p-3">
      <div class="row">
        <div class="col-md-5 m-auto">
          <a class="btn btn-light mt-1" @click.prevent="$router.go(-1)"></a>
          <h4 class="display-4 text-center"></h4>
          <div class="d-block mb-1 text-muted small">*</div>

          <!--  -->
          <form @submit.prevent="submit">
            <TextField
              type="type"
              name="handle"
              placeholder="*"
              v-model="msgInfo.handle"
              :error="errors.handle"
              info="handle"
            />
            <SelectList
              name="status"
              :error="errors.status"
              v-model="msgInfo.status"
              :options="options"
              info=""
            />
            <TextField
              type="type"
              name="company"
              placeholder=""
              v-model="msgInfo.company"
              :error="errors.company"
              info=""
            />
            <TextField
              type="type"
              name="website"
              placeholder=""
              v-model="msgInfo.website"
              :error="errors.website"
              info=""
            />
            <TextField
              type="type"
              name="location"
              placeholder=""
              v-model="msgInfo.location"
              :error="errors.location"
              info=""
            />
            <TextField
              type="type"
              name="skills"
              placeholder="*"
              v-model="msgInfo.skills"
              :error="errors.skills"
              info="HTMLJSCSSPHPJAVA"
            />
            <TextField
              type="type"
              name="githubusername"
              placeholder="Github"
              v-model="msgInfo.githubusername"
              :error="errors.githubusername"
              info="github"
            />
            <TextArea
              name="bio"
              placeholder=""
              v-model="msgInfo.bio"
              :error="errors.bio"
              info=""
            />

            <div class="mb-3">
              <button
                class="btn btn-light"
                @click.prevent="displaySocialInput=!displaySocialInput"
              ></button>
              <span class="text-muted"></span>
            </div>
            <div v-show="displaySocialInput">
              <InputGroup
                placeholder=""
                name="wechat"
                v-model="msgInfo.wechat"
                :error="errors.wechat"
                icon="fab fa-weixin"
              />
            </div>
            <div v-show="displaySocialInput">
              <InputGroup
                placeholder="QQ"
                name="wechat"
                v-model="msgInfo.QQ"
                :error="errors.QQ"
                icon="fab fa-qq"
              />
              <div v-show="displaySocialInput">
                <InputGroup
                  placeholder=""
                  name="phone"
                  v-model="msgInfo.phone"
                  :error="errors.phone"
                  icon="fas fa-phone-square"
                />
              </div>
            </div>
            <div v-show="displaySocialInput">
              <InputGroup
                placeholder=""
                name="tengxunkt"
                v-model="msgInfo.tengxunkt"
                :error="errors.tengxunkt"
                icon="fab fa-internet-explorer"
              />
            </div>
            <div v-show="displaySocialInput">
              <InputGroup
                placeholder=""
                name="wangyikt"
                v-model="msgInfo.wangyikt"
                :error="errors.wangyikt"
                icon="fab fa-internet-explorer"
              />
            </div>

            <input type="submit" class="btn btn-info btn-block mt-4 mb-4" />
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import TextField from "./common/TextFieldGroup";
import TextArea from "./common/TextAreaGroup";
import SelectList from "./common/SelectListGroup";
import InputGroup from "./common/InputGroup";
export default {
  name: "CreateProfile",
  data() {
    return {
      msgInfo: {
        handle: "",
        company: "",
        status: "*",
        website: "",
        location: "",
        skills: "",
        githubusername: "",
        QQ: "",
        wechat: "",
        phone: "",
        tengxunkt: "",
        wangyikt: "",
      },
      options: [
        { id: "0", value: "*" },
        { id: "Front-end junior engineer", value: "" },
        { id: "Front end senior engineer", value: "" },
        { id: "Junior engineer at the back end", value: "" },
        { id: "Back-end senior engineer", value: "" },
        { id: "Full stack engineer", value: "" },
        { id: "architect", value: "" },
        { id: "UI designer", value: "UI" },
        { id: "personnel supervisor", value: "" },
        { id: "salesman", value: "" },
        { id: "project manager", value: "" },
        { id: "testing personnel", value: "" },
        { id: "operation and maintenance staff", value: "" },
        { id: "financial executive", value: "" },
        { id: "teacher", value: "" },
        { id: "doctor", value: "" },
        { id: "lawyer ", value: "" },
      ],
      displaySocialInput: false,
      errors: {},
    };
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.getCurrentProfile();
    });
  },
  methods: {
    submit() {
      // console.log(this.msgInfo);
      this.$axios
        .post("/api/profiles", this.msgInfo)
        .then((res) => {
          this.$store.dispatch("setProfile", res.data);
          this.$router.push("/dashboard");

          this.errors = {};
        })
        .catch((err) => {
          if (err.response.data) {
            this.errors = err.response.data;
          }
        });
    },
    getCurrentProfile() {
      let profiles= this.$store.getters.profile;
      // console.log(profiles);
      profiles.company = profiles.company ? profiles.company : "";
      profiles.githubusername = profiles.githubusername ? profiles.githubusername : "";
      profiles.handle = profiles.handle ? profiles.handle : "";
      profiles.location = profiles.location ? profiles.location : "";
      profiles.status = profiles.status ? profiles.status : "";
      profiles.website = profiles.website ? profiles.website : "";
      profiles.bio = profiles.bio ? profiles.bio : "";

      profiles.skills = profiles.skills ? profiles.skills.join(","): "";

      profiles.social = profiles.social ? profiles.social : {};
      profiles.wechat = profiles.social.wechat? profiles.social.wechat: "";
      profiles.QQ = profiles.social.QQ ? profiles.social.QQ : "";
      profiles.phone = profiles.social.phone ? profiles.social.phone : "";
      profiles.tengxunkt = profiles.social.tengxunkt? profiles.social.tengxunkt: "";
      profiles.wangyikt = profiles.social.wangyikt? profiles.social.wangyikt : "";

      this.msgInfo = profiles;
    },
  },
  components: {
    TextField,
    TextArea,
    SelectList,
    InputGroup,
  },
};
</script>

<style scoped>
</style>



