<template>
  <div class="education">
    <h4 class="mb-2"></h4>
    <table class="table ">
      <thead>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr v-for="edu in education" :key="edu._id">
          <td>{{edu.school}}</td>
          <td>{{edu.degree}}</td>
          <td>{{edu.from}}~{{edu.to}}</td>
          <td>
            <button @click="deleteClick(edu._id)" class="btn btn-danger"></button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "Education",
  props: {
    education: Array,
  },
  methods: {
    deleteClick(id) {
       this.$emit('deleteEducation', id);;
    },
  },
};
</script>

<style scoped>
</style>



